import React from "react";

const Project = props => {
  const { title, description, image, githubLink, youtubeLink } = props;

  return (
    <article className="project">
      <img src={image} alt="Preview" className="preview" />
      <p>{title}</p>
      <p>{description}</p>
      <a href={githubLink} />
      <a href={youtubeLink} />
    </article>
  );
};

export default Project;
